<template>
  <section class='body'>
    <div class='container'>
      <div style='display: flex; justify-content: center; align-items: center'>
        <h1 style='margin: 20px; font-size: 30px; text-align: center'>Statistics</h1>
        <v-btn outlined style='margin-right: 10px; color: #519945' @click='createItem()'>Add new card</v-btn>
      </div>
      <div style='display: flex;justify-content: space-between; align-items: center; flex-wrap: wrap'>
        <div
          v-for='item in statistics'
          :key='item.id'
          class='card'
        >
          <div>
            <div style='margin-bottom: 6px;'>
              Name: {{ item.name }}
            </div>
            <div style='margin-bottom: 6px;'>
              Purchases: {{ item.purchases }}
            </div>
            <div style='margin-bottom: 6px;'>Notes:{{ item.notes || '-' }}</div>
          </div>

          <v-card-actions>
            <v-btn outlined style='margin-right: 10px' @click='open(item)'>
              Edit
            </v-btn>
            <v-btn style='margin-left: 10px' @click='deleteItem(item.id)'>
              Delete
            </v-btn>
          </v-card-actions>
        </div>
      </div>
    </div>

    <div v-if='modalOpen' class='modal'>
      <div class='modal-content'>
        <span class='close' @click='close()'>&times;</span>
        <h2 style='margin: 20px auto; font-size: 24px; text-align: center'>{{
            isEdit ? 'Edit Card' : 'Create Card'
          }}</h2>
        <form>
          <div style='margin-bottom: 10px;'>
            <label for='name'>Name:</label>
            <input id='name' v-model='editedItem.name' required>
          </div>
          <div v-if='isEdit' style='margin-bottom: 10px;'>
            <label for='purchases'>Purchases:</label>
            <input id='purchases' v-model='editedItem.purchases' required type='number'>
          </div>
          <div>
            <label for='notes'>Notes:</label> <br>
            <textarea
              id='notes'
              v-model='editedItem.notes'
              style='background: white; width: 100%; min-height: 100px'
            ></textarea>
          </div>
          <div style='display: flex; justify-content: center; margin-top: 20px;'>
            <v-btn @click='save()'>
              Save
            </v-btn>
          </div>
        </form>
      </div>
    </div>
    <div style='position: absolute; top: 10px; right: 20px'>
      <v-btn outlined style='color: #a5a5a5;' @click='toLogout()'>Logout</v-btn>
    </div>

  </section>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: 'UserView',
  data() {
    return {
      statistics: [],
      loading: false,
      modalOpen: false,
      isEdit: false,
      editedItem: {}
    }
  },
  computed: {
    ...mapState({
      stat: (s) => s.statistics
    })
  },
  async mounted() {
    this.loading = true
    await this.getStat()
    this.statistics = this.stat
    this.loading = false
  },
  methods: {
    ...mapActions({
      getStat: 'getStat',
      update: 'updateCard',
      create: 'createCard',
      delete: 'deleteCard',
      logout: 'logout'
    }),
    open(item) {
      this.editedItem = { ...item }
      this.modalOpen = true
      this.isEdit = true
    },
    createItem(){
      this.modalOpen = true
    },
    close() {
      this.modalOpen = false
      this.isEdit = false
    },
    async toLogout(){
      await this.logout()
    },
    async save() {
      const data = {
        id: this.editedItem.id,
        name: this.editedItem.name,
        purchases: this.editedItem.purchases,
        notes: this.editedItem.notes
      }
      this.isEdit ? await this.update(data) : await this.create(data)
      this.editedItem = {}
      this.close()
      await this.getStat()
      this.statistics = this.stat
      this.isEdit = false
    },
    async deleteItem(id) {
      await this.delete(id)
      await this.getStat()
      this.statistics = this.stat
    }
  }
}
</script>

