  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-content {
  background: antiquewhite;
  padding: 20px;
  border-radius: 5px;
  width: 400px;
  height: 400px;
}
.close {
  float: right;
  font-size: 28px;
  cursor: pointer;
}
.body{
  position: relative;
}
</style>